import React from "react";
import { AwesomeButton } from "react-awesome-button";
import "react-awesome-button/dist/styles.css";
import "../style/About.css";

function About() {
  return (
    <div className="about-content">
      <h3 className="about-title">
        Jeyhun Jeyhunzade <span>|</span> Front-End Developer
      </h3>
      <p className="about">
        I have been programming in Javascrip(ES6+) for one year, and React for 3
        months (in IBATech-Dan.IT Bootcamp). I have a solid understanding of
        Bootstrap and React frameworks , Git and Github. And core of Python. I
        am eager to learn new technologies.
      </p>
      <AwesomeButton
        href="https://github.com/MrJeyhun"
        target="_blank"
        type="primary"
        className="socialBtns"
      >
        Go to GitHub
      </AwesomeButton>
      <p className="mobile">Mobile ready, just swipe it ;)</p>
    </div>
  );
}

export default About;
