import React from "react";
import "../style/Card.css";
export default function Card(props) {
  const { titleOne, titleTwo, heading, info, img, url } = props;
  return (
    <div>
      <a href={url} target="_blank">
        <figure
          style={{
            backgroundImage: `url(
              ${img}
            )`
          }}
        >
          <div className="date">
            <span className="card-date-day">{titleOne}</span>
            <span className="card-date-month">{titleTwo}</span>
          </div>
          <figcaption>
            <h4>
              <span>{heading}</span>
            </h4>
            <p>{info}</p>
          </figcaption>
        </figure>
      </a>
    </div>
  );
}
