import React from "react";
import "../style/Skills.css";
import Skill from "./Skill";
import html from "../img/html-5.svg";
import css from "../img/css-3.svg";
import js from "../img/js.svg";
import react from "../img/react.png";
import bootstrap from "../img/bootstrap.svg";
import sass from "../img/sass.svg";
import jquery from "../img/jquery2.png";
import redux from "../img/redux.png";
import git from "../img/git.png";
import gulp from "../img/gulp.svg";
import webpack from "../img/webpack.svg";
import npm from "../img/npm.svg";
import github from "../img/github.svg";
import emmet from "../img/emmet.svg";
import jsx from "../img/jsx.png";
import python from "../img/python.svg";

function Skills() {
  return (
    <div className="skills">
      <h3 className="skills-title">Skills</h3>
      <div className="container">
        <Skill imgs={[html, css, bootstrap, sass]} />
        <Skill imgs={[js, jsx, jquery, react]} />
        <Skill imgs={[npm, webpack, gulp, redux]} />
        <Skill imgs={[git, github, emmet, python]} />
      </div>
    </div>
  );
}

export default Skills;
